/** @jsx h */

/**
 * External Dependencies
 */
import { h } from "preact";
import classnames from "classnames";

const Button = ({ href = "#", className, children, onClick }) => {
  return (
    <a
      href={href}
      target="blank"
      onClick={onClick}
      className={classnames("tiktok-feed__button", className)}
    >
      {children}
    </a>
  );
};

export default Button;
