/** @jsx h */

/**
 * External Dependencies
 */
import { h, createContext } from "preact";
import { useReducer, useContext } from "preact/hooks";

const defaultState = () => {
  return {
    currentFeedIndex: 0,
    setIsOpen: false,
  };
};

const FeedModalContext = createContext({});

const useFeedModalContext = () => {
  return useContext(FeedModalContext);
};

const reducer = (state, action) => {
  switch (action.type) {
    case "SET_CURRENT_FEED_INDEX": {
      return {
        ...state,
        ...action.payload,
      };
    }
    case "SET_IS_OPEN": {
      return {
        ...state,
        ...action.payload,
      };
    }
  }
};

const FeedModalProvider = (props) => {
  const { children } = props;

  const [state, dispatch] = useReducer(reducer, defaultState());

  const setCurrentFeedIndex = (currentFeedIndex) => {
    if (state.currentFeedIndex == currentFeedIndex) {
      return;
    }

    dispatch({
      type: "SET_CURRENT_FEED_INDEX",
      payload: {
        currentFeedIndex: currentFeedIndex,
      },
    });
  };

  const setIsOpen = (isOpen) => {
    if (state.isOpen == isOpen) {
      return;
    }

    dispatch({
      type: "SET_IS_OPEN",
      payload: {
        isOpen: isOpen,
      },
    });
  };

  return (
    <FeedModalContext.Provider
      value={{
        ...state,
        setCurrentFeedIndex,
        setIsOpen,
      }}
    >
      {children}
    </FeedModalContext.Provider>
  );
};

const LibraryConsumer = FeedModalContext.Consumer;

export { FeedModalProvider, LibraryConsumer, useFeedModalContext };
