/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";
/**
 * Internal Dependencies
 */
import VideoMask from "./video-mask";
import { videoPlay } from "../../../icons";

const Video = ({ feedItem, feedSettings }) => {
	return (
		<div className="tiktok-feed-item__video-wrap">
			<img
				width={feedItem.width}
				height={feedItem.height}
				alt="TikTok"
				className="tiktok-feed-item__video"
				loading={feedSettings.lazy && "lazy"}
				src={feedItem.cover_image_url}
			/>
			{feedSettings.mask.display && (
				<VideoMask feedItem={feedItem} feedSettings={feedSettings} />
			)}
			<a
				className="tiktok-feed-item__icon tiktok-feed-item__icon--views_count"
				href={feedItem.share_url}
				target="_blank"
			>
				{videoPlay}
				<span>
					{feedItem.views_count}
				</span>
			</a>
		</div>
	);
};

export default Video;
