/**
 * External Dependencies
 */
import { useState, useEffect } from "preact/hooks";
/**
 * Internal Dependencies
 */
import { apiFetch } from "./api-fetch";

async function fetchFeedVideoList(feedSettings, createTime = "") {
	const { source, id, limit } = feedSettings;
	const restRoutePath = qlttf?.restRoutePaths?.[source];

	if (!restRoutePath) {
		throw new Error(`Unknow source: ${source}`);
	}

	if (!restRoutePath) {
		throw new Error(`Unknow source: ${source}`)
	}

	if (!!createTime) console.log('creating more...')

	if (restRoutePath.includes('?')) {
		return await apiFetch(`${restRoutePath}&feed_id=${id}&create_time=${createTime}&limit=${limit}`)
	}

	return await apiFetch(`${restRoutePath}?feed_id=${id}&create_time=${createTime}&limit=${limit}`)
}

export const useFeedVideoList = (feedSettings) => {
	const [isLoading, setIsLoading] = useState(true);
	const [isLoadingMore, setIsLoadingMore] = useState(false);
	const [feedListItems, setFeedVideoList] = useState([]);
	const [createTime, setCreateTime] = useState(null);
	const [error, setError] = useState(null);

	useEffect(() => {
		const fetchingFeedVideoList = async () => {
			setIsLoadingMore(true);
			try {
				const response = await fetchFeedVideoList(feedSettings, createTime);
				setFeedVideoList([...feedListItems, ...response]);
				setIsLoading(false);
				setIsLoadingMore(false);
			} catch (error) {
				setIsLoading(false);
				setIsLoadingMore(false);
				setError(error);
			}
		};
		fetchingFeedVideoList();
	}, [createTime]);

	return {
		isLoading,
		isLoadingMore,
		feedListItems,
		error,
		setCreateTime,
	};
};
