/** @jsx h */

/**
 * External Dependencies
 */
import classNames from "classnames";
import { h, Fragment } from "preact";
import { forwardRef, createPortal } from "preact/compat";
/**
 * Internal Dependencies
 */
import FeedItem from "../feed-item";
import FeedModal from "../feed-modal";
import { useFeedModalContext } from "../../provider";
import { WithStyle } from "./with-style";

const FeedList = (props) => {
	const container = document.querySelector(".tiktok-feed-feed").parentNode;

	const { forwardRef, feedListItems, feedSettings, className, userProfile } =
		props;

	const { currentFeedIndex, setCurrentFeedIndex } = useFeedModalContext();

	const { setIsOpen } = useFeedModalContext();

	const showFeedModal = !!feedSettings?.modal?.display;
	const isLink = !showFeedModal

	return (
		<>
			<div
				ref={forwardRef}
				className={classNames("tiktok-feed-list", className)}
				data-feed_layout={feedSettings.layout}
			>
				{feedListItems.map((feedItem, index) => {
					return (
						<FeedItem
							index={index}
							feedItem={feedItem}
							feedSettings={feedSettings}
							onClick={() => {
								setCurrentFeedIndex(index);
								setIsOpen(true);
							}}
							isLink={isLink}
						/>
					);
				})}
			</div>
			{showFeedModal &&
				createPortal(
					<FeedModal
						userProfile={userProfile}
						feedSettings={feedSettings}
						currentFeedIndex={currentFeedIndex}
						feedListItems={feedListItems}
					/>,
					container
				)}
		</>
	);
};

export default forwardRef(WithStyle(FeedList));
