/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";
import { useCallback } from "preact/hooks";
/**
 * Internal Dependencies
 */
import FeedList from "../../feed-list";

const HightlightSquare = (props) => {
  const { feedListItems, feedSettings } = props;

  const highlightRef = useCallback(
    (ref) => {
      const calculateWidth = () => {
        const widthColumn = ref.querySelector(
          ".tiktok-feed-item:not(.highlight)"
        ).offsetWidth;
        const parent = ref.offsetWidth;
        const gap = feedSettings.video.spacing;

        return parent / (widthColumn - gap);
      };

      if (!!ref) {
        const msnry = new window.Masonry(ref, {
          itemSelector: ".tiktok-feed-item",
          isResizable: true,
          isAnimated: false,
          transitionDuration: 0,
          percentPosition: true,
          columnWidth: calculateWidth(),
        });
      } else return;
    },
    [feedListItems]
  );

  return (
    <FeedList ref={highlightRef} className="tiktok-feed-square" {...props} />
  );
};

export default HightlightSquare;
