/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";
/**
 * Internal Dependencies
 */
import Masonry from "./masonry";
import Carousel from "./carousel";
import Gallery from "./gallery";
import HighlightSquare from "./highlight-square";
import Hightlight from "./highlight";
import CarouselVertical from "./carousel-vertical";

const Layout = (props) => {
  const { feedSettings } = props;

  const { layout } = feedSettings;

  switch (layout) {
    case "masonry":
      return <Masonry {...props} />;
    case "carousel":
      return <Carousel {...props} />;
    case "carousel-vertical":
      return <CarouselVertical {...props} />;
    case "highlight":
      return <Hightlight {...props} />;
    case "highlight-square":
      return <HighlightSquare {...props} />;
    default:
      return <Gallery {...props} />;
  }
};

export default Layout;
