/** @jsx h */

/**
 * External Dependencies
 */
import { h } from "preact";
import classnames from "classnames";
/**
 * Internal Dependencies
 */
import Video from "./video";
import { _isHightligFeedItem, _isCarouselLayout } from "./helpers";
import VideoCard from "./video-card";

const FeedItem = ({ index, feedSettings, feedItem, onClick, isLink }) => {
	const isHightligFeedItem = _isHightligFeedItem(feedSettings, feedItem, index);
	const isCarouselFeedItem = _isCarouselLayout(feedSettings);

	const Tag = isLink ? "a" : "div";

	return (
		<Tag
			className={classnames(
				"tiktok-feed-item",
				"ig-image-loaded",
				isCarouselFeedItem && "swiper-slide",
				isHightligFeedItem && "highlight",
				`tiktok-feed-item--cols-${feedSettings.columns}`
			)}
			data-elementor-open-lightbox="no"
			data-feed-item-index={index}
			onClick={onClick}
			target={isLink ? "_blank" : undefined}
			href={isLink ? feedItem.share_url : undefined}
			title={isLink ? feedItem.video_description : undefined}
		>
			<div className="tiktok-feed-item__wrap">
				<Video feedItem={feedItem} feedSettings={feedSettings} />
				{feedSettings.card.display && (
					<VideoCard feedItem={feedItem} feedSettings={feedSettings} />
				)}
			</div>
		</Tag>
	);
};

export default FeedItem;
