/** @jsx h */

/**
 * External Dependencies
 */
import { memo } from "preact/compat";
import { h } from "preact";

const Profile = ({ feedSettings, userProfile }) => {
  return (
    <div className="tiktok-feed__profile">
      <div className="avatar">
        <img
          src={
            !!feedSettings.profile.avatar
              ? feedSettings.profile.avatar
              : userProfile.avatar
          }
          alt={
            !!feedSettings.profile.nickname
              ? feedSettings.profile.nickname
              : userProfile.nickname
          }
        />
      </div>
      <div className="info">
        <div>
          <span className="user">
            {!!feedSettings.profile.username
              ? feedSettings.profile.username
              : userProfile.username}
          </span>
          <span className="separator">•</span>
          <a
            className="follow"
            href={
              !!feedSettings.profile.username
                ? `https://www.tiktok.com/@${feedSettings.profile.username}`
                : userProfile.link
            }
            target="_blank"
            rel="noopener"
          >
            {!!feedSettings.profile.link_text
              ? feedSettings.profile.link_text
              : "Follow"}
          </a>
          <div className="biography">
            {!!feedSettings.profile.biography
              ? feedSettings.profile.biography
              : userProfile.biography}
          </div>
        </div>
      </div>
    </div>
  );
};

export default memo(Profile);
