/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";

export const WithStyle = (Component) => {
  const HOC = (props, ref) => {
    const { feedSettings } = props;

    const isBoxDisplayed = !!feedSettings.box.display;
    const isCarouselVertical = feedSettings.layout == "carousel-vertical";
    const isCarousel = feedSettings.layout == "carousel";
    const isDiferentFromCarousel = feedSettings.layout != "carousel";
    const isDiferentFromCarouselVertical = feedSettings.layout != "carousel-vertical";
    const isCardDisplayed = !!feedSettings.card.display
    const isMaskDisplayed = !!feedSettings.mask.display
    return (
      <>
        <style>
          {//box styles
            isBoxDisplayed && `
              #tiktok-feed-feed-${feedSettings.id} {
                background-color: ${feedSettings.box.background}; 
                color: ${feedSettings.box.text_color}; 
                border-radius: ${feedSettings.box.radius}px;
                padding: ${feedSettings.box.padding}px;
              }
            `
          }

          {//carousel styles
            (isCarousel || isCarouselVertical) && `
              #tiktok-feed-feed-${feedSettings.id} .swiper-container .swiper-button-prev,
              #tiktok-feed-feed-${feedSettings.id} .swiper-container .swiper-button-next {
                color: ${feedSettings.carousel.navarrows_color}
              }

              #tiktok-feed-feed-${feedSettings.id} .swiper-container .swiper-pagination .swiper-pagination-bullet {
                background-color: ${feedSettings.carousel.pagination_color}
              }
            `
          }

          {//video styles
            (isDiferentFromCarousel && isDiferentFromCarouselVertical) && `
              #tiktok-feed-feed-${feedSettings.id} .tiktok-feed-item {
                padding: ${feedSettings.video.spacing}px;
              }
            `
          }
          {//video styles
            `
              #tiktok-feed-feed-${feedSettings.id} .tiktok-feed-item__video-wrap {
                border-radius: ${feedSettings.video.radius}px; 
                overflow: hidden;
              }
            `
          }

          {//card styles
            isCardDisplayed && `
              #tiktok-feed-feed-${feedSettings.id} .tiktok-feed-item__video-card {
                background-color: ${feedSettings.card.background};
                color: ${feedSettings.card.text_color};
                font-size: ${feedSettings.card.font_size}px;
                padding: ${feedSettings.card.padding}px;
                border-radius: ${feedSettings.card.radius}px;
              }

              #tiktok-feed-feed-${feedSettings.id} .tiktok-feed-item__video-card:hover {
                background-color: ${feedSettings.card.background_hover};
              }
            `
          }

          {//Mask styles
            isMaskDisplayed && `
              #tiktok-feed-feed-${feedSettings.id} .tiktok-feed-item__video-mask { 
                background-color: ${feedSettings.mask.background} !important;
              }
            `
          }
        </style>
        <Component {...props} forwardRef={ref} />
      </>
    );
  };

  return HOC;
};
