/** @jsx h */

import { h } from "preact";
/**
 * Internal Dependencies
 */
import { tiktok } from "../../icons";
import Button from "./button";
import { getFeedVideoLink } from "./helpers";
import { WithStyle } from "./with-style";

const Actions = ({ userProfile, feedSettings, onClick, createTime }) => {
	const hasButton = !!feedSettings?.button?.display;
	const hasButtonLoadMore =
		!!feedSettings?.button_load?.display && createTime != 0;

	return (
		<div className="tiktok-feed__actions">
			{hasButton && (
				<Button
					href={getFeedVideoLink(feedSettings, userProfile)}
					target="blank"
					className="tiktok-feed__button--follow"
				>
					{tiktok}
					{feedSettings.button.text}
				</Button>
			)}
			{hasButtonLoadMore && feedSettings.source == "username" && (
				<Button
					className="tiktok-feed__button--load"
					onClick={onClick}
				>
					{tiktok}
					{feedSettings.button_load.text}
				</Button>
			)}
		</div>
	);
};

export default WithStyle(Actions);
