/**
 *  This is a manual function that calculate the height necessary for display the iframe into the modal and to hide the extra content that is not needed with a overflow hidden.
 *
 * @param {number} height : height of the feed element to compare and calculate the extra height to display the iframe correctly.
 * @param {number} width : width of the feed element that will used for the height of the iframe.
 */
const iframeHeightCalculation = (width, height) => {
  const defaultHeight = 1024;

  // the division is necessary to match the exact pixels to display the iframe correctly without cutting the video controls
  if (height > defaultHeight) {
    let extraHeight = (height - defaultHeight) / 2;

    return width + extraHeight;
  } else return width;
};

export default iframeHeightCalculation;
