/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";

export const WithStyle = (Component) => {
  const HOC = (props, ref) => {
    const { feedSettings } = props;
    const isModalDisplayed = !!feedSettings.modal.align
    return (
      <>
        <style>
          {isModalDisplayed && `
            #tiktok-feed-modal-${feedSettings.id} .tiktok-feed-modal .tiktok-feed-modal__body .tiktok-feed-modal__video-info .tiktok-feed-modal__video_description {
              text-align: ${feedSettings.modal.align};
            }
          `}
        </style>
        <Component {...props} forwardRef={ref} />
      </>
    );
  };

  return HOC;
};
