/** @jsx h */

/**
 * External Dependencies
 */
import { h, render, Fragment } from "preact";
/**
 * Internal Dependencies
 */
import FeedContainer from "./components/feed-container";
import { FeedModalProvider } from "./provider";

(async () => {
  const feeds = document.querySelectorAll(".tiktok-feed-feed");

  Array.from(feeds).forEach(async (target) => {
    const feedSettings = JSON.parse(target.dataset.feed) || {};
    render(
      <>
        <FeedModalProvider>
          <FeedContainer target={target} feedSettings={feedSettings} />
        </FeedModalProvider>
      </>,
      target
    );
  });
})();
