export function getFeedVideoLink(feedSettings, userProfile) {
  const { hashtag, source } = feedSettings;
  const { username } = userProfile;

  switch (source) {
    case "username":
      return `https://www.tiktok.com/@${username}`;

    case "hashtag":
      return `https://www.tiktok.com/tag/${hashtag}`;

    default:
      return "https://www.tiktok.com";
  }
}
