/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";
import { useCallback } from "preact/hooks";
/**
 * Internal Dependencies
 */
import { useFeedModalContext } from "../../../provider";
import FeedList from "../../feed-list";

const CarouselVertical = (props) => {
  const { currentFeedIndex, setCurrentFeedIndex } = useFeedModalContext();
  const { setIsOpen } = useFeedModalContext();

  const { feedListItems, feedSettings } = props;
  /**
   * using useEffect and Ref isn't safe because mutating the reference won't trigger a render, therefore, won't trigger the useEffect.
   * instead of, we need to use useCallback ref hook
   * https://medium.com/@teh_builder/ref-objects-inside-useeffect-hooks-eb7c15198780
   */
  const carouselRef = useCallback(
    (ref) => {
      if (!!ref) {
        const swiper = new window.Swiper(ref, {
          slideClass: "tiktok-feed-item",
          loop: true,
          slidesPerView: feedSettings.carousel.slidespv,
          spaceBetween: feedSettings.video.spacing,
          centeredSlides: true,
          autoplay: feedSettings.carousel.autoplay && {
            delay: feedSettings.carousel.autoplay_interval,
          },
          pagination: {
            el: ".swiper-pagination",
            dynamicBullets: true,
            clickable: true,
            type: "bullets",
          },
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
          },
          breakpoints: {
            320: {
              slidesPerView: 1,
              spaceBetween: 1,
            },
            480: {
              spaceBetween: feedSettings.video.spacing,
              slidesPerView: Math.min(2, feedSettings.carousel.slidespv),
            },
            768: {
              spaceBetween: feedSettings.video.spacing,
              slidesPerView: Math.min(3, feedSettings.carousel.slidespv),
            },
            1024: {
              spaceBetween: feedSettings.video.spacing,
              slidesPerView: feedSettings.carousel.slidespv,
            },
          },
        });

        /**
         * por defecto, swiper con la opcion loop: true, agrega versiones duplicadas de los items en el carousel, esto es para llenar los huecos en blanco y para
         * poder recorrer el carousel de forma infinita, el problema de estos items duplicados es que no cuentan con los scripts de sus versiones originales, ni tampoco se actualizaran
         * a un posible cambio, para resolver este problema se usa el evento onClick de javascript en los elementos duplicados a traves del ref
         */
        const addOnclickToDuplicated = () => {
          ref.addEventListener("click", (e) => {
            if (
              !!e.target.classList.contains("swiper-slide-duplicate") ||
              !!e.target.closest(".swiper-slide-duplicate")
            ) {
              setIsOpen(true);
              setCurrentFeedIndex(
                Number(
                  e.target.closest(".swiper-slide-duplicate").dataset
                    .feedItemIndex
                )
              );
              console.log(e.target.classList);
            } else return;
          });
        };

        addOnclickToDuplicated();
      } else return;
    },
    [feedListItems]
  );

  return (
    <div ref={carouselRef} className="swiper-container">
      <FeedList className="swiper-wrapper" {...props} />

      {feedSettings.carousel.pagination && (
        <div className="swiper-pagination"></div>
      )}

      {feedSettings.carousel.navarrows && (
        <>
          <div tabindex="1" className="swiper-button-prev"></div>
          <div tabindex="1" className="swiper-button-next"></div>
        </>
      )}
    </div>
  );
};

export default CarouselVertical;
