/**
 * External Dependencies
 */
import { useState, useEffect } from "preact/hooks";
/**
 * Internal Dependencies
 */
import { apiFetch } from "./api-fetch";

async function fetchUserProfile(id = 0, source) {
  switch (source) {
    case "username":
      if (qlttf.restRoutePaths.username_profile.includes("?")) {
        return await apiFetch(
          `${qlttf.restRoutePaths.username_profile}&feed_id=${id}`
        );
      }
      return await apiFetch(
        `${qlttf.restRoutePaths.username_profile}?feed_id=${id}`
      );  
    case "account":
      if (qlttf.restRoutePaths.account_profile.includes("?")) {
        return await apiFetch(
          `${qlttf.restRoutePaths.account_profile}&open_id=${id}`
        );
      }
      return await apiFetch(
        `${qlttf.restRoutePaths.account_profile}?open_id=${id}`
      );  
    default:
      break;
  }
}

export const useFeedProfile = (feedSettings) => {
	const [isLoading, setIsLoading] = useState(true);
	const [feedProfile, setFeedProfile] = useState(null);
	const [error, setError] = useState(null);

  const {id, open_id, source} = feedSettings;
  
	useEffect(() => {
		setIsLoading(true);
			const fetchingUserProfile = async () => {
				try {
					const response = await fetchUserProfile(source === "account" ? open_id : id, source);
					setFeedProfile(response);
					setIsLoading(false);
				} catch (error) {
					setIsLoading(false);
					console.error(error);
					setError(error);
				}
			};
			fetchingUserProfile();
	}, [source]);

	return {
		isLoading,
		feedProfile,
		error,
	};
};
