/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";

const Alert = ({ errorUserProfile, errorUserVideoList }) => {
  return (
    <div className="tiktok-feed__alert">
      <ul>
        {errorUserProfile && (
          <li>
            <b>{errorUserProfile.error}</b>
            <p>{errorUserProfile.message}</p>
          </li>
        )}
        {errorUserVideoList && (
          <li>
            <b>{errorUserVideoList.error}</b>
            <p>{errorUserVideoList.message}</p>
          </li>
        )}
      </ul>
    </div>
  );
};

export default Alert;
