/** @jsx h*/

/**
 * External Dependencies
 */
import { h } from "preact";
import { memo } from "preact/compat";
/**
 * Internal Dependencies
 */
import { filterDescription } from "../helpers";

const VideoCard = ({ feedSettings, feedItem }) => {
  return (
    <div class="tiktok-feed-item__video-card">
      <div class="tiktok-feed-item__info">
        {<div class="tiktok-feed-item__date">{feedItem.date}</div>}
        <div class="tiktok-feed-item__icons">
          {!!feedSettings.card.likes_count && (
            <span class="tiktok-feed-item__likes_count">
              <i class="qlttf-icon-heart"></i>
              {feedItem.likes_count}
            </span>
          )}
          {!!feedSettings.card.comments_count && (
            <span class="tiktok-feed-item__comments_count">
              <i class="qlttf-icon-comment"></i>
              {feedItem.comments_count}
            </span>
          )}
        </div>
      </div>
      {!!feedSettings.card.video_description && (
        <p class="tiktok-feed-item__video_description">
          {filterDescription(
            feedItem.video_description,
            feedSettings.card.max_word_count
          )}
        </p>
      )}
    </div>
  );
};

export default memo(VideoCard);
