/** @jsx h */

/**
 * External Dependencies
 */
import { h } from "preact";

const Spinner = () => {
  const spinnerUrl = window?.qlttf.settings?.spinner_image_url;

  return (
    <div
      className="tiktok-feed__spinner"
      style={spinnerUrl && `background-image: url(${spinnerUrl})`}
    />
  );
};

export default Spinner;
