/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";
import { useEffect } from "preact/hooks";
/**
 * Internal Dependencies
 */
import { useFeedVideoList, useFeedProfile } from "../../helpers";
import Actions from "../actions";
import Spinner from "../spinner";
import Alert from "../alert";
import Profile from "../profile";
import Layout from "../layouts";

const FeedContainer = (props) => {
	const { target, feedSettings } = props;

	const {
		isLoading: isLoadingUserProfile,
    feedProfile,
		error: errorUserProfile,
	} = useFeedProfile(feedSettings);

	const {
		isLoading: isLoadingUserVideoList,
		isLoadingMore,
		feedListItems,
		error: errorUserVideoList,
		setCreateTime,
	} = useFeedVideoList(feedSettings);

	const isLoading = isLoadingUserProfile || isLoadingUserVideoList;
	const hasErrors = errorUserProfile || errorUserVideoList;
  const userProfile = !["account", "username"].includes(feedSettings.source) ? feedSettings.profile : feedProfile

  useEffect(() => {
		if (!isLoading) {
			target.classList.add("loaded");
		}
	}, [isLoading]);

	if (!!isLoading) {
		return <Spinner />;
	}

	if (!!hasErrors) {
		return (
			<Alert
				errorUserProfile={errorUserProfile}
				errorUserVideoList={errorUserVideoList}
			/>
		);
	}

	return (
		<>
			{!!feedSettings?.profile?.display && (
				<Profile feedSettings={feedSettings} userProfile={userProfile} />
			)}
			<Layout
				feedSettings={feedSettings}
				feedListItems={feedListItems}
				userProfile={userProfile}
			/>

			{isLoadingMore && <Spinner />}

			<Actions
				userProfile={userProfile}
				feedSettings={feedSettings}
				onClick={(e) => {
					e.preventDefault();
					setCreateTime(feedListItems[feedListItems.length - 1]?.create_time);
				}}
				createTime={feedListItems[feedListItems.length - 1]?.create_time}
			/>
		</>
	);
};

export default FeedContainer;
