/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";

export const WithStyle = (Component) => {
  const HOC = (props) => {
    const { feedSettings } = props;
    
    const isButtonDisplayed = !!feedSettings.button.display;
    const isButtonLoadMoreDisplayed = !!feedSettings.button_load.display;
    return (
      <>
        <style>
          {//button tiktok styles
            isButtonDisplayed && 
              `
                #tiktok-feed-feed-${feedSettings.id} .tiktok-feed__actions .tiktok-feed__button--follow {
                  background-color: ${feedSettings.button.background};
                }

                #tiktok-feed-feed-${feedSettings.id} .tiktok-feed__actions .tiktok-feed__button--follow:hover {
                  background-color: ${feedSettings.button.background_hover};
                }
              `
          }

          {//button load more styles
            isButtonLoadMoreDisplayed && 
              `
                #tiktok-feed-feed-${feedSettings.id} .tiktok-feed__actions .tiktok-feed__button--load {
                  background-color: ${feedSettings.button_load.background};
                }

                #tiktok-feed-feed-${feedSettings.id} .tiktok-feed__actions .tiktok-feed__button--load:hover {
                  background-color: ${feedSettings.button_load.background_hover};
                }
              `
          }
        </style>
        <Component {...props} />
      </>
    );
  };

  return HOC;
};
