/** @jsx h */

/**
 * External Dependencies
 */
import { h } from "preact";
import { useState, useEffect } from "preact/hooks";
/**
 * Internal Dependencies
 */
import { useFeedModalContext } from "../../provider";
import VideoContainer from "./video-container";
import VideoInfo from "./video-info";
import ButtonArrow from "./button-arrow";
import ButtonClose from "./button-close";
import Overlay from "./overlay";

const FeedModal = ({ feedListItems, feedSettings, userProfile }) => {
  const { currentFeedIndex, setCurrentFeedIndex } = useFeedModalContext();

  const { isOpen, setIsOpen } = useFeedModalContext();

  const [currentFeedItem, setCurrentFeedItem] = useState(
    feedListItems[currentFeedIndex]
  );

  const hasPrevVideo = !!feedListItems[currentFeedIndex - 1];
  const hasNextVideo = !!feedListItems[currentFeedIndex + 1];

  const moveToPreviousFeed = () => {
    if (hasPrevVideo) {
      setCurrentFeedIndex(currentFeedIndex - 1);
    } else {
      setCurrentFeedIndex(feedListItems.length - 1);
    }
  };

  const moveToNextFeed = () => {
    if (hasNextVideo) {
      setCurrentFeedIndex(currentFeedIndex + 1);
    } else {
      setCurrentFeedIndex(0);
    }
  };

  useEffect(() => {
    setCurrentFeedItem(feedListItems[currentFeedIndex]);
  }, [currentFeedIndex]);

  return (
    <Overlay id={`tiktok-feed-modal-${feedSettings.id}`}>
      <div className="tiktok-feed-modal" onClick={(e) => e.stopPropagation()}>
        <div className="tiktok-feed-modal__options">
          <div className="tiktok-feed-modal__options--arrows">
            <ButtonArrow
              isPrevious
              onClick={(e) => {
                e.stopPropagation();
                moveToPreviousFeed();
              }}
            />
            <ButtonArrow
              next
              onClick={(e) => {
                e.stopPropagation();
                moveToNextFeed();
              }}
            />
          </div>
          <ButtonClose onClick={() => setIsOpen(false)} />
        </div>
        <section className="tiktok-feed-modal__body">
          <VideoContainer
            feedSettings={feedSettings}
            currentFeedItem={currentFeedItem}
          />
          <VideoInfo
            feedSettings={feedSettings}
            currentFeedItem={currentFeedItem}
            userProfile={userProfile}
            feedItemsNumber={feedListItems.length}
          />
        </section>
      </div>
    </Overlay>
  );
};

export default FeedModal;
