/**
 * This function checks multiples conditions to handle the highlight that depends of the positon, tags or id.
 * @param {*} feedSettings settings of feeds
 * @param {*} feedItem object of the feed
 * @param {Number} index position of the feed
 * @returns Boolean
 */
export function _isHightligFeedItem(feedSettings, feedItem, index) {
  const isLayoutHightlight = ["highlight", "highlight-square"].includes(
    feedSettings.layout
  );

  if (!isLayoutHightlight) {
    return false;
  }

  if (!feedSettings?.highlight) {
    return false;
  }

  for (const prop in feedSettings.highlight) {
    const arr = feedSettings.highlight[prop].split(",").map((element) => {
      return element.toString().trim();
    });
    switch (prop) {
      case "position":
        const position = arr.includes(index.toString());
        if (position) {
          return true;
        }
        break;
      case "id":
        const id = arr.includes(feedItem.id);
        if (id) {
          return true;
        }
        break;
      case "tag":
        const tag = feedItem.tags?.filter((tag) => {
          return StringToHashtagArray(feedSettings).includes(tag);
        }).length;

        if (tag) {
          return true;
        }
        break;
    }
  }

  return false;
}

/**
 * Check if the layout is a carousel
 * @param {*} feedSettings settings of feeds
 * @returns boolean
 */
export function _isCarouselLayout(feedSettings) {
  return ["carousel", "carousel-vertical"].includes(feedSettings.layout);
}

/**
 * Transform a string to an array, check if a word have # and add it if necessary.
 * @param {*} feedSettings settings of feeds
 * @returns array of hashtags
 */
export const StringToHashtagArray = (feedSettings) => {
  const tagsArray = feedSettings.highlight.tag?.split(" ");

  const hashtagsArray = tagsArray.map((word) => {
    if (!word.includes("#")) {
      return word
        .split(" ")
        .map((letter) => "#" + letter) //add # to the beginning of the word
        .join(" ");
    } else return word;
  });

  return hashtagsArray;
};

/**
 * Filter description that include html characters and limit the count of words
 * @param {string} description
 * @param {Number} max_word_count number of words to show
 * @returns string
 */
export const filterDescription = (description, max_word_count) => {
  const limitedText = description
    .replace(/<[^>]+>/g, "")
    .split(" ", max_word_count)
    .join(" ");
  //if the new length from the settings of card is less than the original text length, then add ... at the end of the text
  if (limitedText.length < description.length) {
    return `${limitedText}...`;
  } else return limitedText;
};
