/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";
import { useCallback } from "preact/hooks";
import { useFeedModalContext } from "../../../provider";
/**
 * Internal Dependencies
 */
import FeedList from "../../feed-list";

const Carousel = (props) => {
  const { feedListItems, feedSettings } = props;
  const { setCurrentFeedIndex } = useFeedModalContext();
  const { setIsOpen } = useFeedModalContext();

  const carouselRef = useCallback(
    (ref) => {
      if (!!ref) {
        const swiper = new window.Swiper(ref, {
          slideClass: "tiktok-feed-item",
          loop: true,
          loopedSlidesLimit: false,
          centeredSlides: true,
          slidesPerView: feedSettings.carousel.slidespv,
          spaceBetween: feedSettings.video.spacing,
          autoplay: feedSettings.carousel.autoplay && {
            delay: feedSettings.carousel.autoplay_interval,
          },
          pagination: {
            el: ".swiper-pagination",
            dynamicBullets: true,
            clickable: true,
            type: "bullets",
          },
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
          },
          breakpoints: {
            320: {
              slidesPerView: 1,
              spaceBetween: 1,
            },
            480: {
              spaceBetween: feedSettings.video.spacing,
              slidesPerView: Math.min(2, feedSettings.carousel.slidespv),
            },
            768: {
              spaceBetween: feedSettings.video.spacing,
              slidesPerView: Math.min(3, feedSettings.carousel.slidespv),
            },
            1024: {
              spaceBetween: feedSettings.video.spacing,
              slidesPerView: feedSettings.carousel.slidespv,
            },
          },
        });

        // add manually the same scripts from the original feeds to the duplicated created by the swiper that by default have no scripts
        const addOnclickToDuplicated = () => {
          ref.addEventListener("click", (e) => {
            if (
              !!e.target.classList.contains(".swiper-slide-duplicate") ||
              !!e.target.closest(".swiper-slide-duplicate")
            ) {
              setIsOpen(true);
              setCurrentFeedIndex(
                Number(
                  e.target.closest(".swiper-slide-duplicate").dataset
                    .feedItemIndex
                )
              );
            } else return;
          });
        };

        addOnclickToDuplicated();
      } else return;
    },
    [feedListItems]
  );

  return (
    <div ref={carouselRef} className="swiper-container">
      <FeedList className="swiper-wrapper tiktok-feed-square" {...props} />

      {feedSettings.carousel.pagination && (
        <div className="swiper-pagination"></div>
      )}

      {feedSettings.carousel.navarrows && (
        <>
          <div tabindex="1" className="swiper-button-prev"></div>
          <div tabindex="1" className="swiper-button-next"></div>
        </>
      )}
    </div>
  );
};

export default Carousel;
