/** @jsx h */

/**
 * External Dependencies
 */
import { h } from "preact";
/**
 * Internal Dependencies
 */
import { logo } from "../../../icons";
import { useFeedModalContext } from "../../../provider";
import { WithStyle } from "./with-style";

const VideoInfo = ({
  currentFeedItem,
  userProfile,
  feedItemsNumber,
  feedSettings,
}) => {
  const { currentFeedIndex } = useFeedModalContext();

  const isDiferentFromUsername = !!currentFeedItem.profile;

  return (
    <section className="tiktok-feed-modal__video-info">
      {!!feedSettings.modal.profile && (
        <div className="tiktok-feed-modal__profile">
          <img
            src={
              isDiferentFromUsername
                ? currentFeedItem.profile.avatar
                : userProfile.avatar
            }
          />
          <div>
            <span>
              {isDiferentFromUsername
                ? currentFeedItem.profile.nickname
                : userProfile.nickname}
            </span>
            <a
              href={
                isDiferentFromUsername
                  ? currentFeedItem.profile.link
                  : userProfile.link
              }
              title={
                isDiferentFromUsername
                  ? currentFeedItem.profile.nickname
                  : userProfile.nickname
              }
              target="_blank"
              rel="noopener"
            >
              {`@${
                isDiferentFromUsername
                  ? currentFeedItem.profile.username
                  : userProfile.username
              }`}
            </a>
          </div>
        </div>
      )}
      <div className="tiktok-feed-modal__icons">
        <span className="tiktok-feed-modal__counter">{`${
          currentFeedIndex + 1
        } / ${feedItemsNumber}`}</span>
        <a
          className="tiktok-feed-modal__link"
          href={currentFeedItem.share_url}
          target="_blank"
          rel="noopener"
        >
          {logo}TikTok
        </a>
        {/* {!!feedSettings.modal.download && (
					<a className="tiktok-feed-modal__link" href={currentFeedItem.download_url} target="_blank" rel="noopener"><i class="qlttf-icon-download"></i>Download</a>
				)} */}
      </div>
      {!!feedSettings.modal.video_description && (
        <p
          className="tiktok-feed-modal__video_description"
          dangerouslySetInnerHTML={{
            __html: currentFeedItem.video_description,
          }}
        />
      )}
      <div className="tiktok-feed-modal__info">
        {!!feedSettings.modal.likes_count && (
          <span className="tiktok-feed-modal__likes_count">
            <i className="qlttf-icon-heart"></i>
            {currentFeedItem.likes_count}
          </span>
        )}
        {!!feedSettings.modal.date && (
          <span className="tiktok-feed-modal__date">
            {currentFeedItem.date}
          </span>
        )}
        {!!feedSettings.modal.comments_count && (
          <span className="tiktok-feed-modal__comments_count">
            <i className="qlttf-icon-comment"></i>
            {currentFeedItem.comments_count}
          </span>
        )}
      </div>
    </section>
  );
};

export default WithStyle(VideoInfo);
