/** @jsx h */

/**
 * External Dependencies
 */
import { h, Fragment } from "preact";

const VideoMask = ({ feedSettings, feedItem }) => {
  return (
    <>
      <div className="tiktok-feed-item__video-mask" />
      <div className="tiktok-feed-item__video-mask--content">
        {!!feedSettings.mask.likes_count && (
          <span className="tiktok-feed-item__likes_count--mask">
            <i className="qlttf-icon-heart tiktok-feed-item__icon-heart"></i>
            {feedItem.likes_count}
          </span>
        )}
        {!!feedSettings.mask.comments_count && (
          <span className="tiktok-feed-item__comments_count--mask">
            <i className="qlttf-icon-comment tiktok-feed-item__icon-bubble_chat"></i>
            {feedItem.comments_count}
          </span>
        )}
      </div>
    </>
  );
};

export default VideoMask;
