/** @jsx h */

/**
 * External Dependencies
 */
import classnames from "classnames";
import { h, Fragment } from "preact";
import { useState } from "preact/hooks";
import { memo } from "preact/compat";
/**
 * Internal Dependencies
 */
import useScript from "../../../hooks/useScript";
import Spinner from "../../spinner";
import iframeHeightCalculation from "./helpers";

const VideoContainer = ({ currentFeedItem, feedSettings }) => {
  const [isLoading, setIsLoading] = useState(true);

  useScript("https://www.tiktok.com/embed.js", currentFeedItem);

  return (
    <section
      className={classnames(
        "tiktok-feed-modal__video-container",
        !!currentFeedItem.embed_html
          ? "tiktok-feed-modal__video-container--iframe"
          : "tiktok-feed-modal__video-container--video"
      )}
    >
      {!!currentFeedItem.embed_html ? (
        <div
          className="tiktok-feed-modal__iframe-container"
          dangerouslySetInnerHTML={{ __html: currentFeedItem.embed_html }}
          style={`height: ${iframeHeightCalculation(
            currentFeedItem.width,
            currentFeedItem.height
          )}px`}
        />
      ) : (
        <>
          <video
            autoplay={!!feedSettings.modal.autoplay}
            className="tiktok-feed-modal__video"
            controls={!!feedSettings.modal.controls}
            controlsList="nodownload"
            onLoadStart={() => setIsLoading(true)}
            onLoadedData={() => setIsLoading(false)}
            style={!!isLoading && { display: "none" }}
            src={currentFeedItem.video_url}
            type="video/mp4"
          />

          {!!isLoading && (
            <div className="tiktok-feed-modal__spinner">
              <img
                alt="TikTok"
                className="tiktok-feed-modal__spinner--img"
                src={currentFeedItem.cover_image_url}
              />
              <Spinner />
            </div>
          )}
        </>
      )}
    </section>
  );
};

export default memo(VideoContainer);
